import { useNavigate } from 'react-router-dom';
import Button from '@mui/material/Button';
const MainPage = () => {
  const navi = useNavigate();

  return (
    <>
      <section id="hero">
        <h2>신선한 재료, 정성 가득한 한 입</h2>
        <p>
          Tiny Bite는 매일 아침 받는 신선한 재료로 정직하게 만드는 수제 햄버거
          전문점입니다. 인기 있는 클래식 버거부터 치즈 버거, 채식 버거까지 가족
          모두가 즐길 수 있어요.
        </p>
        <img
          width="400px"
          src="https://cdn.pixabay.com/photo/2022/07/15/18/12/cheese-burger-7323674_1280.jpg"
          alt="hamburger"
        />
        <Button
          variant="contained"
          onClick={() => {
            navi('/board/list');
          }}
        >
          게시판으로
        </Button>
      </section>

      <section id="menu">
        <h2>인기 메뉴</h2>
        <article className="menu-item">
          <h3>클래식 버거</h3>
          <p>100% 소고기 패티, 신선한 채소, 특제 소스 — 6,500원</p>
        </article>

        <article className="menu-item">
          <h3>치즈 버거</h3>
          <p>풍성한 치즈와 바삭한 베이컨 — 7,500원</p>
        </article>

        <article className="menu-item">
          <h3>베지 버거</h3>
          <p>건강한 채식 패티와 아보카도 — 7,000원</p>
        </article>
      </section>

      <section id="contact">
        <h2>매장 정보</h2>
        <p>주소: 서울시 강남구 작은로 123</p>
        <p>영업시간: 월~일 11:00 - 21:00</p>
        <p>전화: 010-1234-5678</p>
        <p>이메일: mtyu02345@gmail.com</p>
      </section>

      <section id="newsletter">
        <h2>뉴스레터</h2>
        <form>
          <label for="email">이메일</label>
          <input
            type="email"
            id="email"
            name="email"
            placeholder="이메일을 입력하세요"
          />
          <button type="submit">구독</button>
        </form>
      </section>
    </>
  );
};

export default MainPage;
