import React from 'react';
import styled from '@emotion/styled';
const StyledFooter = styled('footer')`
  padding: 2em;
  background-color: #202020;
  box-sizing: border-box;
  height: 100%;
  overflow: hidden;
`;

const Footer = () => {
  return (
    <>
      <StyledFooter>
        <div>
          <p>🍔 Burger</p>
          <p>
            신선한 재료와 정성으로 만든 수제버거를 제공합니다. 오늘도 맛있게!
          </p>
          <p>
            문의: <span>mtyu02345@gmail.com</span>
          </p>
          <p>
            © <span>2025</span> Burger. All rights reserved.
          </p>
        </div>
      </StyledFooter>
    </>
  );
};

export default Footer;
