import React, { useEffect, useState } from 'react';
import Button from '@mui/material/Button';
import { useNavigate } from 'react-router-dom';
import Box from '@mui/material/Box';
import img01 from '../../img/hamburger01.jpg';
import {
  StyledBoxDiv,
  StyledBoxDiv2,
  StyledImgDiv,
  StyledTable,
} from '../styles/commonStyle';

const BoardList = () => {
  const navi = useNavigate();
  const [voList, setVoList] = useState([]);

  useEffect(() => {
    getList();
  }, []);

  function getList() {
    setVoList(JSON.parse(localStorage.getItem('boardList')) || []);
  }

  return (
    <>
      <h1>게시판</h1>
      <StyledBoxDiv>
        <StyledBoxDiv2>
          <StyledTable>
            <thead>
              <tr>
                <th>번호</th>
                <th>제목</th>
                <th>내용</th>
              </tr>
            </thead>
            <tbody>
              {voList.map((vo, idx) => (
                <tr key={idx}>
                  <td>{idx}</td>
                  <td>{vo.title}</td>
                  <td>{vo.content}</td>
                </tr>
              ))}
            </tbody>
          </StyledTable>
          <Button
            variant="contained"
            onClick={() => {
              navi('/board/insert');
            }}
          >
            등록
          </Button>
        </StyledBoxDiv2>
        <StyledImgDiv>
          <img src={img01} />
        </StyledImgDiv>
      </StyledBoxDiv>
    </>
  );
};

export default BoardList;
